hr {
  min-width: 100%;
}

.headercontainer {
  display: flex;
  font-size: 60px;
  margin: 2rem;
}

.mapsitepage {
  width: 100%;
}

.mapsiteheader {
  display: flex;
  z-index: 3;
}

.mapsitehead {
  color: #fa4454;
}

.mapsitedivider {
  color: #343434;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 35px;
}

.mapsitetext {
  color: whitesmoke;
}

.mapsitepagecard {
  display: flex;
  position: relative;
  flex-direction: column;
  border-radius: 25px;
  margin: 2rem;
  background-color: #505050;
  overflow: hidden;
  z-index: 1;
}

.mapsitepagecardstripe1 {
  width: 8rem;
  height: 110%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-250%, -50%) rotate(15deg);
  z-index: 1;
  background-color: #343434;
}

.mapsitepagecardstripe2 {
  width: 8rem;
  height: 110%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-75%, -50%) rotate(15deg);
  z-index: 1;
  background-color: #343434;
}
